import React, { Component } from 'react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import FeedContextProvider, { FeedConsumer } from '../../GeneralComponents/context/feedContext';
import { UsersContext } from '../../GeneralComponents/context/usersContext';
import './mainContent.style.css';
//components
import NewsFeed from './NewsFeed/NewsFeed.component';
import SingleContent from './NewsFeed/singleContent.component';

class MainContent extends Component {
  constructor(props) {
      super(props);
      this.state = {  }
  }

  static contextType = UsersContext;
  render() { 
    return (
      <main className="main">
        <div className="content-wrapper">
          <Router>
            <FeedContextProvider>
              <FeedConsumer>
                {({updateFeed, feed}) => (
                  <Switch>
                    <Route render={(props) => <NewsFeed path={`${props.match.path}`} updateFeed={updateFeed} feed={feed} props={{...props}} />} />
                    <Route render={(props) => <SingleContent path={"/articles/:articleId" || "/gifs/:gifId"} updateFeed={updateFeed} feed={feed} props={{...props}} />} />
                  </Switch>
                )}
              </FeedConsumer>
            </FeedContextProvider>
          </Router>
        </div>
      </main>
    );
  }
}

export default MainContent;